:root {
    --off-white: #fafafa;
}

.navbar {
    background-color: var(--off-white);
    box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}

.btn1 {
    background: none!important;
    border: none;
    padding: 0!important;
    cursor: pointer;
    font-size: 20px;
}

.link {
    text-decoration: none;
    color: black;
    font-size: 20px;
    cursor: pointer;
}
.navbar-link:hover {
    color: rgba(0, 0, 0, 0.75)
}

.a-link {
    text-decoration: underline;
    cursor: pointer;
    font-size: 15px;
}

.h5-tag {
    font-size: 15px;
    font-weight: normal;
}

.not-reg {
    display: grid;
}
